@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  font-family: "Outfit", sans-serif;
  font-size: 400;
  margin: 0;
}

body {
  background-color: hsl(212deg, 45%, 89%);
}

.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 8.5% auto;
}
.flex-container .main-content {
  background-color: hsl(0deg, 0%, 100%);
  width: 300px;
  padding: 15px;
  border-radius: 15px;
  margin: 15px;
  text-align: center;
  font-size: 15px;
  box-shadow: 0 0 33px hsl(220deg, 15%, 55%);
}
.flex-container .main-content h1 {
  color: hsl(218deg, 44%, 22%);
  font-size: 1.4em;
  font-weight: 700;
  padding: 15px 8px 8px;
}
.flex-container .main-content p {
  color: hsl(220deg, 15%, 55%);
  padding: 8px 5px 15px;
}
.flex-container .main-content img {
  max-width: 100%;
  border-radius: 10px;
}

a {
  text-decoration: none;
  color: hsl(220deg, 15%, 55%);
}
a:hover {
  color: hsl(218deg, 44%, 22%);
}

footer {
  position: fixed;
  bottom: 5px;
  font-size: 0.8em;
  width: 100%;
  text-align: center;
}

@media only screen and (max-width: 768px) and (min-width: 374px) {
  .flex-container {
    margin: 18% auto;
  }
}
@media only screen and (max-width: 375px) {
  .flex-container {
    margin: 30% auto;
  }
}